const { Socket } = require('engine.io-client');
const socket = new Socket('ws://' + document.location.host);
//@ts-ignore
socket.on('open', (err, data) => {
    console.log('connected', err, data);
    // @ts-ignore
    socket.on('message', (data) => { });
    socket.on('close', () => {
        console.log('closed')
    })
})
